.item{
    margin-top: 30px;
    margin-bottom: 20px;
    padding: 10px;
    border-radius: 10px;
    border: 2px solid #ccc;
    background-color: rgba(100, 110, 255, 0.3);
}

.item img{
    width: 50px;
    height: 50px;
    border-radius: 25px;
    float: left;
}

.message {
    margin-left: 50px;
    padding: 10px;
    margin-bottom: 10px;
}

.like{
    margin-top: 15px;
    margin-left: 10px;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    color: #546b9c;
}

.like:hover{
    color: #ff8234;
}

.likesCounts {
    display: inline-block;
    margin-left: 5px;
    font-size: 15px;
    padding-bottom: 5px;
}
