.profile_info {
    margin-bottom: 30px;
    overflow: hidden;
}

.img img{
    max-width: 100%;
    margin-left: 5px;
}

.myProfile{
    margin-top: 20px;
}

.myProfile .ava {
    float: left;
    margin-left: 5%;
    margin-right: 5%;
}

.ava img {
    max-width: 150px;
    margin-top: 10px;
}

.info{
    overflow: hidden;
}

.name h2{
    margin-top: 0;
    margin-bottom: 5px;
}

.profileStatus{
    color: #545466;;
    margin-bottom: 10px;
}

.profileStatus input{
    min-width: 180px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-family: Roboto,sans-serif;
    color: #37374b;
    width: 30%;
    padding: 2px;
}

.item{
    margin-bottom: 8px;
}

.item .contacts strong{
    float: left;
    display: block;
    margin-right: 5px;
}

.contactsItems{
    overflow: hidden;
}

.contactsItem{
    margin-bottom: 5px;
}




